.tape-wrapper{perspective:1200px;width:100%;display:flex;justify-content:center;box-sizing:border-box;padding-inline:clamp(.5rem,2vw,1.25rem)}.tape{width:100%;max-width:720px;margin:4rem auto;padding-inline:0;box-sizing:border-box;background-color:var(--card-bg);border:3px solid var(--accent);border-radius:8px;box-shadow:0 10px 30px rgba(0,255,255,.1);font-family:var(--font-oswald),"Orbitron",sans-serif;text-transform:uppercase;color:var(--text);transition:transform .5s ease,box-shadow .4s ease,scale .3s ease}.tape:hover{scale:1.015}.tape__header{background:none;padding:1rem;text-align:center;border-bottom:2px solid var(--card-border)}.tape__header h2{margin:0;font-size:1.4rem;color:var(--accent);text-shadow:0 0 5px rgba(0,255,255,.2)}.tape__header h3{margin-top:.3rem;font-size:.85rem;color:var(--text);font-weight:400;opacity:.75;font-style:italic;text-transform:none}.tape__table{display:block}@media(max-width: 600px){.tape__table{display:none}}.tape__row{display:grid;grid-template-columns:1fr auto 1fr;align-items:stretch;text-align:center;border-bottom:1px solid var(--card-border);overflow:hidden}.tape__row:last-child{border-bottom:none}.tape__row--header{font-size:.85rem;font-weight:bold;color:var(--accent)}.tape__cell{padding:.8rem .6rem;font-size:1rem;font-weight:600}.tape__cell--left{background:linear-gradient(to right, rgba(0, 140, 255, 0.25), transparent);text-align:left;padding-left:1rem;width:100%}.tape__cell--right{background:linear-gradient(to left, rgba(255, 60, 60, 0.25), transparent);text-align:right;padding-right:1rem;width:100%}.tape__cell--label{font-size:.75rem;font-weight:400;opacity:.7;color:var(--text);text-transform:none}.tape__cell--label-side{font-weight:700;font-size:1.4rem;color:var(--accent)}@media(max-width: 600px){.tape__cell--left,.tape__cell--right{text-align:center;padding-left:0;padding-right:0}}.tape__mobile{display:none}@media(max-width: 600px){.tape__mobile{display:flex;flex-direction:column;gap:1rem;padding:1rem}}.tape__row-mobile{display:flex;flex-direction:column;gap:.5rem;border:1px solid var(--card-border);border-radius:8px;padding:.75rem;background-color:var(--card-bg);text-align:center;box-shadow:0 2px 10px rgba(0,255,255,.05)}.tape__label{font-size:.75rem;font-weight:500;opacity:.7;color:var(--accent);text-transform:none}.tape__note{padding:1rem;font-size:.75rem;text-align:center;font-style:italic;color:var(--text);background-color:var(--card-bg);border-top:2px solid var(--card-border);opacity:.75}.tape__vs-badge{text-align:center;font-family:"Orbitron",sans-serif;font-weight:800;font-size:1.25rem;background-color:var(--accent);color:var(--bg);padding:.4rem 1rem;border-radius:999px;width:fit-content;margin:0 auto -1.2rem auto;position:relative;z-index:2;box-shadow:0 0 8px rgba(0,255,255,.6);transform:translateY(-1.2rem);letter-spacing:1px;transition:transform .4s ease}.tape__vs-badge:hover{transform:translateY(-1.2rem) rotateZ(-5deg) scale(1.05)}@media(max-width: 360px){.tape{padding-inline:.5rem}.tape__cell{font-size:.85rem;padding:.6rem .4rem}.tape__vs-badge{font-size:1rem;padding:.3rem .75rem}.tape__header h2{font-size:1.2rem}.tape__header h3{font-size:.75rem}}